import React from "react";



const StockData = props => (
    <div id='info'>
        {props.name && <h2 className="section-title" id={'section-title-'+props.mode}>Company Information </h2>}
        <div className="stock-key" id={'stock-key-'+props.mode}>
            { props.name && <p> Company: { props.name } </p> }
            { props.price && <p> Price: ${ props.price} </p> }
            { props.pe && <p> PE: {props.pe}</p>}
            { props.yearHigh && <p> 52 Week High: {props.yearHigh}</p>}
            { props.yearLow && <p> 52 Week Low: {props.yearLow}</p>}
            { props.exchange && <p> Exchange: {props.exchange}</p>}
            {props.error && <p> {props.error} </p> }
        </div>
    </div>
);

/*

name: data[0].name,
price: data[0].price,
pe: data[0].pe,
yearHigh: data[0]["52_week_high"],
yearLow: data[0]["52_week_low"],
exchange: data[0]["stock_exchange_short"],
error: ""
*/

export default StockData;